





import Vue from "vue";
export type Size = "normal" | "compact" | "invisible";
export default Vue.extend({
  name: "QReCaptcha",
  data() {
    return {
      captchaKeys: "" as String,
      language:""
      
    };
  },
  destroyed() {
    this.$el?.remove();
  },
  props: {
    _renderingProps: {
      type: Object,
      default: null,
    },
    siteKey: {
      type: String
    },
    size: {
      type: String,
      default: "normal",
    },
    tabindex: {
      type: Number
    },
  },
  mounted() {
    let dory = this._renderingProps.context().GetItemSingle("Dory");
    this.language = dory.GetLRType().substring(0, 2);
    let environmentVariable = dory.shellConfigs.getEnvironmentVariable?.();
    this.captchaKeys = this.siteKey ?? environmentVariable?.captchaKeys?.sitekey
    document.querySelectorAll("script[src*=recaptcha__]").forEach((el) => el.remove());
    document.querySelectorAll("div:has(.g-recaptcha-bubble-arrow)").forEach((el) => el.remove());
    let recaptchaScript = document.createElement("script");
    recaptchaScript.id = "recaptchaScript";
    recaptchaScript.src = `https://www.google.com/recaptcha/api.js?hl=${this.language}&onload=onLoadCallBack&render=explicit`;
    recaptchaScript.async = true;
    recaptchaScript.defer = true;
    document.head.appendChild(recaptchaScript);
    
  },
  created() {
    window["onLoadCallBack"] = this.onLoadCallBack;
  },
  methods: {
    onLoadCallBack() {
      grecaptcha.render("divRecaptcha", {
        "sitekey": this.captchaKeys as string,
        "size": this.size as Size,
        "tabindex": this.tabindex,
        "callback": this.successCallBack,
        "expired-callback": this.expiredCallback,
        "error-callback": this.errorCallback
      });
      let captchaFrame = document.querySelector("iframe[src*=recaptcha]") as HTMLInputElement;
      let lang = captchaFrame.src.split("hl=")[1];
      captchaFrame.src = captchaFrame.src.replace(lang.substring(0, 2), this.language);
    },
    successCallBack(token:string) {
      this.$emit("onVerify", token);
    },
    expiredCallback() {
      this.$emit("onFail", {reason: 'expired'});
    },
    errorCallback() {
      this.$emit("onFail", {reason: 'error'});
    }
  },
});
